import React, { useState } from 'react'
import $ from 'jquery';

class Filter extends React.Component {

  //range sliders js
  componentDidMount() {
    $(".price-range-slider").ionRangeSlider({
      type: "double",
      min: 30,
      max: 500,
      from: 120,
      to: 400,
      prefix: 'م.ل.س',
      grid: false,
      skin: "sharp"
    });
    $(".size-range-slider").ionRangeSlider({
      type: "double",
      min: 10,
      max: 500,
      from: 100,
      to: 400,
      prefix: 'قدم',
      grid: false,
      skin: "sharp"
    });
  }
  //End range sliders js

  render() {
    return (
      <section className='filter'>
        <div className='container'>
          <form className='card'>
            <div className='row'>
              <div class="dropdown col-lg">
                <button class="btn dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                  كل المدن
                </button>
                <ul class="dropdown-menu">
                  <li><a class="dropdown-item" href="#">دمشق</a></li>
                  <li><a class="dropdown-item" href="#">حمص</a></li>
                  <li><a class="dropdown-item" href="#">اللاذقية</a></li>
                </ul>
              </div>
              <div class="dropdown col-lg">
                <button class="btn dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                  كل الأنواع
                </button>
                <ul class="dropdown-menu">
                  <li><a class="dropdown-item" href="#">تجاري</a></li>
                  <li><a class="dropdown-item" href="#">سكني</a></li>
                </ul>
              </div>
            </div>
            <div className='row'>
              <div className='price-range col-lg'>
                السعر :
                <input type="text" class="price-range-slider" name="price_range" value="" />
              </div>
              <div className='price-range col-lg'>
                الحجم :
                <input type="text" class="size-range-slider" name="size_range" value="" />
              </div>
            </div>
            <div className='brbr'>
              <button className='btn search' type='submit'><i className='fa fa-search'></i></button>
            </div>
          </form>

        </div>
      </section>
    )
  }
}

export default Filter