import React from 'react'
import Category from './Category'
import Image1 from './images/villa.webp'
import Image2 from './images/villa2.webp'

const Catigories = () => {
    return (
        <section className='catigories'>
            <div className='container'>
                <div className='title'>
                    <h1>الفئات حسب <span>الملكية</span></h1>
                    <small>هنا يوجد أفضل العروض في دمشق وما حولها</small>
                </div>
                <div className='cate1'>
                    <Category link='#' image={Image1} properety='25' name='أرض في دمشق' price='280.000.000' />
                    <Category link='#'  image={Image2} properety='25' name='أرض في دمشق' price='280.000.000' />
                </div>
                <div className='cate2'>
                    <Category link='#'  image={Image2} properety='25' name='أرض في دمشق' price='280.000.000' />
                    <Category link='#' image={Image1} properety='25' name='أرض في دمشق' price='280.000.000' />
                </div> 
            </div>
        </section>
    )
}

export default Catigories